import React from 'react'
import './variants.css';
import { Paper, Divider,Checkbox } from '@material-ui/core';
import TagInput from './tagInputs/TagInput';
import VariantTable from './varianttable/VariantTable';
import {connect} from 'react-redux';
import {setVariants} from '../../../containers/productconf/actions';
import { validate } from '@babel/types';

const Variants = (props) => { 
    let [variants, setVariants] = React.useState([{variantName: "color", tags: [],error:false}])
    let [finalTable,setFinalTable] = React.useState([]);
    let [value,setValue] = React.useState("");
    let [showValue,setShowValue] = React.useState(false);

    React.useEffect(() => {
        if(props.state) { 
            handleFinalTable();
            props.setVariants([...variants]); 
        }
        if(props.variants) { 
            setVariants([...props.variants]); 
        }
    },[props.data,props.state]);

    const handleFinalTable = () => { 
        let arr = []; 
        let i = 0; 
        if(arr.length == 0 && variants.length == 1) {
            variants[0].tags.forEach(tag => {
                arr.push({
                    variant1: tag,  
                    mrp: props.basicInfo.mrp,
                    yourPrice: props.basicInfo.yourPrice,
                    quantity: 1,
                    vId: 'HD78',
                    pId: "384HD",   
                    inStock: true
                })
            });
        } else if(variants.length == 2) {
            let temp = variants[0].tags;
            temp.forEach(val => {
                variants[1].tags.forEach(tag => {
                    arr.push({
                        variant1: val,
                        variant2: tag,  
                        mrp: props.basicInfo.mrp,
                        yourPrice: props.basicInfo.yourPrice,
                        quantity: 1,
                        vId: 'SD1SD',
                        pId: "384HD",   
                        inStock: true
                    })
                });
            })
        } 
        setFinalTable(arr);
    }
    const validate = () => {
        let temp = variants;
        handleFinalTable();
        let value = true;
        temp.forEach(variant => {
            if(variant.tags.length == 0) {
                variant.error = "A Variant Type Should have atleast one variant";
                value = false;
            } else {
                variant.error = false;
            }
        });
        setVariants([...temp]);
        return value;
    }
    const nextPage = () => {
        if(validate()) {
            props.enableVariant();
        }
    }
    return (
        <div className="basic-info">
            <Paper variant="outlined">
                <div className="basic-info-content container-one">
                    <div className="d-flex a-i-c j-c-b">
                        <p className="basic-conf-head">{props.cat && props.cat.categoryname}  {props.sub && props.sub.subCategoryName}</p>
                        <button className="btn-text" onClick={nextPage}>Next Page</button>
                    </div>
                    <Divider /> 
                    <h4>Variants</h4>
                    <div className="check-container d-flex a-i-c">
                        <Checkbox
                            defaultChecked
                            color="primary"
                            inputProps={{ 'aria-label': 'secondary checkbox' }}
                        />
                        <p>I have multiple variants of this product.</p>
                    </div> 
                    {variants.map((variant,i) => {
                        return (
                            <TagInput 
                                name={variant.variantName} 
                                arr={variant.tags} 
                                error={variant.error}
                                onAdd={(val) => { 
                                    let temp = variants;
                                    temp[i].tags.push(val);
                                    setVariants([...temp]);
                                }}
                                onRemove={(val) => {
                                    let temp = variants;
                                    temp[i].tags = temp[i].tags.filter(variant => variant != val)
                                    setVariants([...temp]);
                                }}
                            />
                        )
                    })}
                    <br/>
                    <button className="btn-text"
                        onClick={handleFinalTable}
                    >Apply</button>
                    <button className="btn-outline" onClick={() => setShowValue(true)}>Add Another Variant</button>
                    {showValue  &&
                    <>
                        <input type="text" className="form-control-small" onChange={(e) => setValue(e.target.value)} value={value}/>
                        <button className="btn-outline" onClick={() => {
                            if(value != "") {
                                let temp = variants;
                                variants.push({variantName: value,tags: [],error: false});
                                setVariants([...temp]);
                                setValue("");
                                setShowValue(false)
                            }
                        }}>Add</button> 
                    </>}
                </div>
            </Paper>
            <br/>

            <Paper variant="outlined">
                <div className="basic-info-content container-one">
                    <VariantTable table={finalTable} variants={variants} state={props.state} />
                </div>
            </Paper>
        </div>
    )
}
const mapStateToProps = state => ({
    basicInfo: state.productConf.basicInfo,
    variants: state.productConf.variants
})
export default connect(mapStateToProps,{setVariants})(Variants);