import * as actionTypes from './actionTypes';
import Variant from '../../components/mystore/addExistingProduct/product/variant/Variant';

let initialState = {
    products: false, 
    myProducts: false,
    myProductVariantId: false
}  
const reducer =  (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.SET_PRODUCTS:
            return {...state,products: action.payload}
        case actionTypes.SET_MY_PRODUCTS:
            let arr = [];
            action.payload.forEach(product => { 
                product.sellerVariants.forEach(variant => { 
                    arr.push(variant.variantId)
                });
            }); 
            return {...state,myProducts: action.payload,myProductVariantId: arr}
        default:
            return state
    }
}
  
export default reducer;