import React from 'react'
import Switch from '@material-ui/core/Switch';
import {withRouter} from 'react-router-dom';
import Checkbox from '@material-ui/core/Checkbox';
import './product.css';

const Product = (props) => {
    const [state, setState] = React.useState({
        checkedA: true, 
    });
    const [checked, SetChecked] = React.useState(false);
    const handleChange = (event) => {
        setState({ ...state, [event.target.name]: event.target.checked });
    }; 
    return (
        <div className="product-content">
            <div className="prd-col-1">
                <Checkbox
                    checked={checked}
                    onChange={() => SetChecked(!checked)}
                    inputProps={{ 'aria-label': 'primary checkbox' }}
                />
                <img src={props.variant.variantResources[0] && props.variant.variantResources[0].resourceURL.split("?")[0]} alt="product" className="prd-img" />
            </div> 
            <div className="prd-col-2">
                <div className="prd-row-1">
                    <h5>{props.product.productName} - {props.variant.variantName}</h5>
                </div>
                <div className="prd-row-2">
                    <div className="prd-row-2-content">
                        <div className="product-spec">
                            <p className="prd-key">MRP</p>
                            <p className="prd-value">Rs {props.variant.mrp}</p>
                        </div>
                        <div className="product-spec">
                            <p className="prd-key">Selling Price</p>
                            <p className="prd-value">Rs {props.variant.sellerPrice}</p>
                        </div>
                        <div className="product-spec">
                            <p className="prd-key">Stock Left</p>
                            <p className="prd-value">{props.variant.quantity}</p>
                        </div>
                    </div>
                    
                </div>
            </div>
            <div className="prd-col-3">
                <div className="d-flex a-i-c">
                    <p className="text-conf">Active</p>
                    <Switch
                        checked={state.checkedA}
                        onChange={handleChange}
                        name="checkedA"
                        color="primary"
                        inputProps={{ 'aria-label': 'success checkbox' }}
                    />
                </div>
                <div className="prd-row-2-buttons">
                    {/* <button className="btn-outline" onClick={() => props.history.push("/product-configure")}>Edit Product</button> */}
                    <button className="btn-primary" onClick={() => props.setModal()}>Edit Stock/Price</button>
                </div>
            </div>
        </div>
    )
}
export default withRouter(Product);