import React from 'react';
import './searchbar.css';
import { withStyles } from '@material-ui/core/styles'; 
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select'; 
import InputBase from '@material-ui/core/InputBase';
import search from '../../../assets/icons/search.svg';
 
const BootstrapInput = withStyles((theme) => ({
    input: {
      borderRadius: 2,
      position: 'relative', 
      fontSize: 14,
      padding: '10px 26px 10px 12px',
      transition: theme.transitions.create(['border-color', 'box-shadow']),
    },
}))(InputBase);

const SearchBar = (props) => {
    const [string,setString] = React.useState("");
    return (
        <div className="search-container">
            {props.search
                ?
            <div className="search-content">
                <img src={search} className="search-img hover"  />
                <input type="text" placeholder="Search Products" className="search" value={string} onChange={(e) => {
                    props.onChange(e.target.value)
                    setString(e.target.value)
                }}/>
            </div>
                :
            <div></div>}
            {props.dropDown &&
            <FormControl className="select-box"> 
                <Select
                    labelId="demo-customized-select-label"
                    id="demo-customized-select" 
                    input={<BootstrapInput />}
                    value={props.dropValue}
                    onChange={(val) => props.onDropChange(val)}
                > 
                    {props.dropDownContent.map((val,index) => {
                        return <MenuItem value={index}>{val}</MenuItem>
                    })}
                </Select>
            </FormControl>}
        </div>
    );
}
export default SearchBar;