import React from 'react';
import './generateinvoice.css';
import FullScreen from '../../../utils/fullscreen/FullScreen';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import danger from '../../../../assets/icons/danger.svg';
import Form from '../../../utils/inputs/Input';
import Date from '../../../utils/inputs/Date';
import RadioButton from '../../../utils/inputs/Radio';
import axios from 'axios';
import {showSnackbar} from '../../../../containers/app/action';
import {setInvoice} from '../../../../containers/orders/action';

import {connect} from 'react-redux';

const GenerateInvoice = (props) => {
    const [desc,setDesc] = React.useState("");
    const [loading,setLoading] = React.useState(false);
    const generateInvoice = () => {
        setLoading(true);
        axios({
            method: "get",
            url: `/generateInvoice?orderId=${props.order.orderId}&description=${desc}`,
        }).then(res => {
            window.open(res.data.invoicePath);
            props.setInvoice(props.order.orderId,res.data.invoicePath)
            props.showSnackbar("Invoice Generated Successfully");
            setLoading(false);
            props.onClose();
        }).catch(err => { 
            setLoading(false);
            props.showSnackbar("Error: Generating invoice!")
        })
    }
    return (
        <div>
            <FullScreen open={props.open} onClose={props.onClose} head="Generate Invoice">
                <div className="generate-invoice">
                    <Paper variant="outlined">
                        <div className="invoice-head container-one">
                            <h2>Order Id: <span>{props.order.orderId}</span></h2>
                        </div>
                        <Divider />
                        <div className="invoice-content">
                            <div className="invoice-hint-text">
                                <img src={danger} alt=""/>
                                <span>You will not be able to modify invoice after sending to customer.</span>
                            </div>
                            <div className="invoice-form-content container-one">
                                <Form 
                                    displayName="Serial Number" 
                                    value={desc}
                                    onChange={(value) => setDesc(value)}  
                                />
                                <Form 
                                    displayName="Order Id*" 
                                    value={props.order.orderId} 
                                />
                                <Form 
                                    displayName="Product Name*" 
                                    value={props.order.productName} 
                                />
                                <Form 
                                    displayName="Total Price with Delivery Charge *" 
                                    value={props.order.totalAmount} 
                                />
                                <Form 
                                    displayName="Address *"  
                                    value={props.order.buyerAddress} 
                                />
                                <Date 
                                    displayName="Order Received Date *"  
                                />
                                <RadioButton 
                                    displayName="Payment Mode *" 
                                    value={props.order.paymentMode}
                                    arr={["COD","ONLINE"]} 
                                />
                                {loading ?
                                <button className="btn-blue">
                                    Creating Invoice...
                                </button>
                                    :
                                <button className="btn-blue"
                                    onClick={generateInvoice}
                                >Create Invoice</button>}
                            </div>
                        </div>
                    </Paper>
                </div>
            </FullScreen>            
        </div>
    );
}
export default connect(null,{showSnackbar,setInvoice})(GenerateInvoice);