import React from 'react'
import './taginput.css';

const TagInput = (props) => {
    const [value,setValue] = React.useState("");
    const onHandleChange = (e) => { 
        if(e.keyCode == 13 || e.keyCode == 32) {
            if(value.trim() != "") {
                props.onAdd(value.trim())
                setValue("");
            }
        }
    }
    return (
        <div className="tag-form-container">
            <label htmlFor="name">{props.name}</label>
            <div className="tag-form-content">
                <div className="tag-form-container-div">
                    <input 
                        type="text" 
                        placeholder={`Enter ${props.name}`} style={{textIndent: props.arr.length > 0 && props.arr.length * 70 + 20}}
                        onKeyDown={onHandleChange} 
                        onChange={(e) => setValue(e.target.value)}
                        value={value}   
                    />
                    <div className="tag-container">
                        {props.arr.map(val => {
                            return (
                                <div className="tag">
                                    {val}
                                    <span
                                        onClick={() => props.onRemove(val)}
                                    >x</span>
                                </div>
                            )
                        })}
                    </div>
                </div>
                {props.error && <span className="text-error">{props.error}</span> }
            </div>
        </div>
    );
}
export default TagInput;