import * as actionTypes from './actionTypes';

export const setBasicInfo = (data) => dispatch => {
    dispatch({
        type: actionTypes.SET_BASIC_INFO,
        payload: data
    })
}
export const setDetailedInfo = (data) => dispatch => { 
    dispatch({
        type: actionTypes.SET_DETAILED_INFO,
        payload: data
    })
}
export const addDetailedInfo = (data) => dispatch => { 
    dispatch({
        type: actionTypes.ADD_DETAILED_INFO,
        payload: data
    })
}

export const removeDetailedInfo = (data) => dispatch => { 
    dispatch({
        type: actionTypes.REMOVE_DETAILED_INFO,
        payload: data
    })
}

export const addTitleData = (index,value) => dispatch => { 
    dispatch({
        type: actionTypes.SET_TITLE_VALUE,
        payload: {
            index,
            value
        }
    })
}

export const addDescData = (index,value) => dispatch => { 
    dispatch({
        type: actionTypes.SET_DESC_VALUE,
        payload: {
            index,
            value
        }
    })
}

export const setVariants = (data) => dispatch => {
    dispatch({
        type: actionTypes.SET_VARIANTS,
        payload: data
    })
}

export const setVariantsTable = (data) => dispatch => {
    dispatch({
        type: actionTypes.SET_VARIANTS_TABLE,
        payload: data
    })
}

export const setProductImage = (data) => dispatch => {
    dispatch({
        type: actionTypes.SET_PRODUCT_IMAGE,
        payload: data
    })
}

export const setVariantImage = (data,index) => dispatch => {
    dispatch({
        type: actionTypes.SET_VARIANT_IMAGE,
        payload: {data: data,index: index}
    })
}
