import React from 'react';
import './myaccount.css'; 
import AppBar from '../../components/utils/appbar/AppBar'; 
import TopBar from '../../components/utils/topbar/TopBar'; 
import BasicDetail from '../../components/myaccount/basicdetails/basicdetail/BasicDetail';
import BusinessDetail  from '../../components/myaccount/basicdetails/businessDetail/BusinessDetail';
import Address from '.././../components/myaccount/basicdetails/address/Address';

const Orders = () => {
    const [activeRoute, setRoute] = React.useState(0);
    const [activeIndex,setIndex] = React.useState(0);
    const routes = [
        {
            name: 'Orders',
            func: () => setRoute(0)
        },  
    ];
    const changeIndex = (index) => {
        setIndex(index);
    }
    const components = [
        {
            component: (
                <>
                    <TopBar 
                        head="My Account"   
                        btn={true}
                        button={<button className="btn btn-text" onClick={routes[0].func}>+ Create Coupon</button>}    
                    />
                    <AppBar 
                        routes={routes} 
                        tabs={true}
                        index={activeIndex} 
                        changeIndex={changeIndex} 
                        items={["Basic Details","Payment Details"]} 
                        btn={true}
                        button={<button className="btn btn-text" onClick={routes[0].func}>+ Create Coupon</button>}
                    />
                    <div className="my-account-body">
                        <BasicDetail />
                        <BusinessDetail />
                        <Address />
                    </div>
                </>
            ),
        }   
    ]
    return (
        <div className="main-container">
            {components[activeRoute].component}
        </div>
    );
}

export default Orders;