import React from 'react';
import './mystore.css';

import AppBar from '../../components/utils/appbar/AppBar';
import SearchBar from '../../components/utils/searchbar/SearchBar';
import ProductList from '../../components/mystore/productlist/ProductList';
import CategoryList from '../../components/mystore/categorylist/CategoryList';
import TopBar from '../../components/utils/topbar/TopBar';
import PropModal from '../../components/utils/propmodal/PropModal';
import AddEProductList from '../../components/mystore/addExistingProduct/AddEProductList';
import search from '../../assets/icons/search.svg';
import description from '../../assets/icons/description.svg'
import plus from '../../assets/icons/add.svg';

import {connect} from 'react-redux';
import {getProducts,getMyProducts} from './action';

const MyStore = (props) => {
    const [activeRoute, setRoute] = React.useState(0);
    const [activeIndex,setIndex] = React.useState(0);
    const [addProduct,setAddProduct] = React.useState(false);
    const [searchText,setSearchText] = React.useState("");
    const [catState,setCatState] = React.useState("");
    const [activeCatId,setActiveCatId] = React.useState(1);
    const [activeSubCatId,setActiveSubCatId] = React.useState(1);
    const [dropValue,setDropValue] = React.useState(0);

    React.useEffect(() => {
        if(!props.myProducts) {
            props.getMyProducts(); 
        } 
    },[props.products]);

    const routes = [
        {
            name: 'Product',
            func: () => setRoute(0)
        }, {
            name: 'Category List',
            func: () => setRoute(1)
        },{
            name: 'Add from existing Product',
            func: () => setRoute(3)
        }
    ];
    const changeIndex = (index) => {
        setIndex(index);
    }
    const components = [
        {
            component: (
                <>
                    <TopBar 
                        head="My Store" 
                        back={false} 
                        btn={true}
                        button={<button className="btn btn-blue" onClick={() => setAddProduct(true)}>+ Add Product</button>}
                    />
                    <AppBar 
                        routes={routes} 
                        tabs={true}
                        index={activeIndex} 
                        changeIndex={changeIndex} 
                        items={["Products (33)","In Stock(0)","Store banner(0)"]} 
                        btn={true}
                        button={<button className="btn btn-blue" onClick={() => setAddProduct(true)}>+ Add Product</button>}
                    />
                    <SearchBar 
                        search={true}
                        head={"Show Active Product"}
                    />
                    <ProductList routes={routes} products={props.myProducts} />
                </>
            ),
        },{
            component: (
                <>
                    <TopBar 
                        head="Choose Category" 
                        back={true} 
                        backClick={routes[0].func} 
                        btn={true}
                        button={<button className="btn btn-text" onClick={() => {}}>+ Add Category</button>}
                    />
                    <AppBar 
                        tabs={false}
                        btn={true}
                        button={<button className="btn btn-text" onClick={() => {}}>+ Add Category</button>}
                    />
                    <CategoryList onComplete={(catId,subCatId) => {
                        setActiveCatId(catId);
                        setActiveSubCatId(subCatId); 
                        props.history.push(`product-configure/${catId}/${subCatId}`) 
                    }}/>
                </>
            ),
        },{
            component: (
                <>
                    <TopBar 
                        head="Choose Category" 
                        back={true} 
                        backClick={routes[0].func} 
                        btn={true}
                        button={<button className="btn btn-text" onClick={() => {}}>+ Request Category</button>}
                    />
                    <AppBar 
                        tabs={false}
                        btn={true}
                        button={<button className="btn btn-text" onClick={() => {}}>+ Request Category</button>}
                    />
                    <CategoryList 
                        onComplete={(catId,subCatId) => {
                            setActiveCatId(catId);
                            setActiveSubCatId(subCatId); 
                            props.getProducts(catId,subCatId);
                            setRoute(3); 
                        }}
                        catMapping={true} 
                    />
                </>
            ),
        }, {
            component: (
                <>
                    <TopBar 
                        head="Add from existing Product" 
                        back={true} 
                        backClick={routes[0].func} 
                        btn={true}
                        button={<button className="btn btn-blue" onClick={() => setAddProduct(true)}>+ Add Product</button>}
                    /> 
                    <SearchBar 
                        search={true}
                        dropDown={true}
                        dropDownContent={["All","Not in Store"]}
                        onDropChange={(e) => { 
                            setDropValue(e.target.value)
                            console.log(e.target.value)
                        }}
                        dropValue={dropValue}
                        value={searchText}
                        onChange={(value) => setSearchText(value)}
                        head={"Show Active Product"}
                    /> 
                    <AddEProductList
                        searchString={searchText}
                        products={props.products}
                        catId={activeCatId}
                        dropValue={dropValue}
                        subCatId={activeSubCatId}
                    />
                </>
            ),
        }
    ];
    return (
        <div className="main-container">
            {components[activeRoute].component}
            <PropModal open={addProduct} onClose={() => setAddProduct(false)}>
                <div className="add-product-nav">
                    <div className="add-product-nav-content hover" onClick={() => {
                        setRoute(2)
                        setCatState("add-from-existing-product");
                        setAddProduct(false);
                    }}>
                        <img src={search} alt="search" />
                        <span>Search and add existing Product</span>
                    </div>
                    <div className="add-product-nav-content hover" onClick={() => {
                        setRoute(1)
                        setCatState("add-product");
                        setAddProduct(false);
                    }}>
                        <img src={plus} alt="search" />
                        <span>Add new product</span>
                    </div>
                    <div className="add-product-nav-content hover" onClick={() => {
                        setRoute(0)
                        setAddProduct(false);
                    }}>
                        <img src={description} alt="search" />
                        <span>Add new variant to product</span>
                    </div>
                </div>
            </PropModal>
        </div>
    );
}

const mapStateToProps = state => ({
    products: state.mystore.products,
    myProducts: state.mystore.myProducts
})
export default connect(mapStateToProps, {getProducts, getMyProducts})(MyStore);