import React from "react";
import "./signin.css";    
import axios from 'axios';
import eye from '../../../assets/icons/eye.svg'
import eyeclose from '../../../assets/icons/eye-close.svg'   

const SignIn = (props) => {
  const [formData,setFormData] = React.useState({email:"",password:""}); 
  const [error,setError] = React.useState(false);
  const [loading, setloading] = React.useState(false);

  const [formError, setFormError] = React.useState({
      email: false, 
      password: false
  });
  const [visible,setVisible] = React.useState("password") 

  const validator = () => { 
    let valid = true;
    let error = {email: false,password: false};
    Object.keys(formData).map(key => {
        if(formData[key] == "") { 
            error[key] = `* ${key} field cannot be empty`;
            valid = false;
        }
    });
    setFormError({...error})
    return valid;
  } 
  const loginHandler = (e) => {
    e.preventDefault();
    setError(false);
    if(validator()) {
      setloading(true);
      axios({
        method:"get",
        url: "/signin", 
        headers: {
          loginId: formData.email,
          password: formData.password,
        },
      }).then((res)=>{
        setloading(false);  
        localStorage.setItem("isUserLoggedIn", true); 
        window.location.reload();
      }).catch(err =>{ 
        if(err.response) {
          if(err.response.data.errorcode == "LG02") { 
            axios({
                url: "/signout"
            }).then(() => {
                setError("Something Went Wrong Try Again");
                setloading(false);
            }).catch(err => { 
                setloading(false);
            })
          } else {
            setError(err.response.data.message)
            setloading(false);
          }
        } else {
          setloading(false);
        }
      });
    }
  }; 
  const handleChange = (e) => {
    e.persist();
    let val = e.target.value;
    setFormData({...formData,[e.target.name]: val})
  } 
  return (  
        <div className="sign-in">
          <div className="sign-in-container">
            <div className="sign-in-content">
              <h1>Login</h1> 
              <form className="login-form">
                {error && <div className="text-danger">{error}</div>}
                <div className="form-group">
                  <input 
                      type="email" 
                      placeholder="Email"
                      name="email"  
                      onChange = {handleChange}
                      className="form-control" 
                      value={formData.email} 
                      />
                  {formError.email && <span className="text-danger">{formError.email}</span>}
                </div>

                <div className="form-group">
                  <div className="form-icon-grp">
                      <input 
                          type={visible} 
                          placeholder="Password"  
                          name="password"
                          onChange = {handleChange}
                          className="form-control"
                          value={formData.password}
                      />  
                      {visible == "password"
                        ?
                      <img src={eye} className="form-icon" onClick={() => setVisible("text")}/>
                        :
                      <img src={eyeclose} className="form-icon" onClick={() => setVisible("password")}/>}
                  </div>
                  {formError.password && <span className="text-danger">{formError.password}</span>}
                </div> 
              </form>
            </div>
            <footer className="d-flex signin-footer d-flex j-c-b flex-wrap a-i-c">
              <div className="d-flex align-items-center"> 
                <p className="m-1 link hover" onClick={props.otpLogin}>
                  Login with OTP
                </p>
              </div>
              {loading
                  ?
              <button className="button-blue"> 
              Loading...</button>
                  :
              <button className="button-blue" onClick={loginHandler}>Sign In</button>
              }
            </footer>
          </div>
        </div>  
  );
};
 
export default SignIn;
