import React from 'react'
import './index.css';

const Input = (props) => {
    return (
        <div className="form-group">
            <label htmlFor="input">{props.displayName ? props.displayName : "" }</label>
            <div className="d-flex a-i-c flex-column">
                <input 
                    type={`${props.type ? props.type : "text"}`} 
                    className="form-control" 
                    placeholder={props.displayName}
                    onChange={(e) => props.onChange(e.target.value)}
                    value={props.value}
                />
                {props.error && <span className="text-error">{props.error}</span>}
            </div>
            {props.removeIcon && <span className="remove-form-icon" onClick={props.onRemove}>x</span>}
        </div>
    );
}
export default Input;