import React from 'react'; 
import Modal from '@material-ui/core/Modal';
import './propmodal.css';
import close from '../../../assets/icons/close.svg';

const PropModal = (props) => {  
  const [open, setOpen] = React.useState(props.open);

  React.useEffect(() => { 
    setOpen(props.open);
  },[props.open]);
  return (
    <div> 
      <Modal
        open={open}
        onClose={props.onClose} 
      > 
          <div className="modal-md-content">
            <img src={close} alt="modal-close" className="modal-close" onClick={props.onClose} /> 
            {props.children}
          </div> 
      </Modal>
    </div>
  );
} 
export default PropModal;