import React from 'react'
import './detailedinfo.css';
import { Paper, Divider } from '@material-ui/core';
import danger from '../../../assets/icons/danger.svg';
import Specification from './specification/Specification';
import {connect} from 'react-redux';
import {setDetailedInfo,
    addDetailedInfo,
    removeDetailedInfo,
    addTitleData,
    addDescData
} from '../../../containers/productconf/actions';

const DetailedInfo = (props) => {
    const [spec,setSpec] = React.useState([{title: "",desc: ""}]);
    React.useEffect(() => {
        if(props.state) { 
            props.setDetailedInfo(spec); 
        }
        if(props.detailedInfo.length > 0) { 
            setSpec([...props.detailedInfo]); 
        }
    },[props.state]); 
    return (
        <div className="basic-info">
            <Paper variant="outlined">
                <div className="basic-info-content">
                <p className="basic-conf-head container-one">{props.cat && props.cat.categoryname}  {props.sub && props.sub.subCategoryName}</p>
                    <Divider />
                    {props.error &&
                        <div className="flash-alert">
                            <div className="flash-content">{props.error}</div>
                        </div>
                    }
                    <div className="instruction">
                        <div className="ins-content">
                            <span><img src={danger} alt="danger-icon" /> More you put detailed information more chances of customer consider to buy it</span> 
                        </div>
                    </div> 
                    {spec.map((val, i) => {
                        return (
                            <Specification 
                                index={i} 
                                value={spec[i]} 
                                onTitleChange={(value) => {
                                    let temp = spec;
                                    temp[i].title  = value;
                                    setSpec([...temp]);
                                    props.addTitleData(i,value)
                                }}
                                onDescChange={(value) => {
                                    let temp = spec;
                                    temp[i].desc  = value;
                                    setSpec([...temp]); 
                                    props.addDescData(i,value)
                                }}
                                remove={() => {
                                    let temp = spec;
                                    temp = temp.filter((val,index) => index != i);
                                    setSpec([...temp]); 
                                    props.removeDetailedInfo(i)
                                }} 
                            />                             
                        );
                    })} 
                    <div className="form-div">
                        <label htmlFor="none"></label>
                        <button className="btn-text"
                            onClick={() => { 
                                setSpec([...spec,{title: "", desc: ""}]);
                                props.addDetailedInfo();
                            }}
                        >+ Add More</button>
                    </div>
                </div>
            </Paper>
        </div>
    )
}
const mapStateToProps = state => ({
    detailedInfo: state.productConf.detailedInfo
});
export default connect(mapStateToProps,{
    setDetailedInfo,
    addDetailedInfo,
    removeDetailedInfo,
    addTitleData,
    addDescData
})(DetailedInfo);