import React from 'react'
import { Storage } from 'aws-amplify';
import { makeStyles } from '@material-ui/core/styles';
import LinearProgress from '@material-ui/core/LinearProgress';

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
});
const Dashboard = () => {
    const [file,setFile] = React.useState();
    const [name,setName] = React.useState();
    const classes = useStyles();
    const [progress, setProgress] = React.useState(0);

    const [displayImg, setDisplayImg] = React.useState(false);
    const handleSubmit = () => {
        let temp = Date.now().toString() +  Math.random();
        let fileName = temp + name; 
        Storage.put(fileName,file,{
            progressCallback(progress) {
                let prog = parseInt(progress.loaded/progress.total*100)
                setProgress(prog)  
            },
            contentType: 'image/png'
        }).then((res) => {
            console.log(res)
            Storage.get(fileName)
                .then(url => setDisplayImg(url))
                .catch(err => console.log(err));
        })
        .catch(err => {
            console.log(err);
        })
    }
    return (
        <div>
            Dashboard Page on Development 

            <input type="file" onChange={(e) => {
                const file = e.target.files[0];
                setFile(file);
                setName(file.name);
            }}/>
            <button onClick={handleSubmit}> Upload</button>

            <LinearProgress variant="determinate" value={progress} />

            <div style={{marginTop: "200px;"}}>.</div>
            <img src={displayImg}  width="400" height="200" alt=""/>
        </div>
    )
}

export default Dashboard;