import React from 'react'
import Paper from '@material-ui/core/Paper';
import SmallInput from '../../../utils/inputs/SmallInput';
import './businessdetail.css';
import Radio from '@material-ui/core/Radio';
import Chip from '@material-ui/core/Chip';

const BusinessDetail = () => {
    const [selectedValue, setSelectedValue] = React.useState('a');
    const [chips,setChips] = React.useState([]);
    const [value,setValue] = React.useState("");
    const handleChange = (event) => {
        setSelectedValue(event.target.value);
    };
    const onHandleChange = (e) => { 
        if(e.keyCode == 13 || e.keyCode == 32) {
            if(value.trim() != "") {
                setChips([...chips,value]);
                setValue("");
            }
        }
    }
    return (
        <div className="business-detail container-one">
            <Paper variant="outlined">
                <div className="business-detail-content">
                    <h1>Business Details</h1>
                    <div className="business-detail-input-container">
                        <div className="delivery-service">
                            <p>Do you need delivery services?</p>
                            <div className="radio-content">
                                <div>
                                    <label htmlFor="yes">Yes</label>
                                    <Radio
                                        checked={selectedValue === 'a'}
                                        onChange={handleChange}
                                        value="a"
                                        color="primary"
                                        name="radio-button-demo"
                                        inputProps={{ 'aria-label': 'A' }}
                                    />
                                </div>
                                <div>
                                    <label htmlFor="yes">No</label>
                                    <Radio
                                        checked={selectedValue === 'a'}
                                        onChange={handleChange}
                                        value="a"
                                        color="primary"
                                        name="radio-button-demo"
                                        inputProps={{ 'aria-label': 'A' }}
                                    />
                                </div>
                                <div>
                                    <label htmlFor="yes">Maybe</label>
                                    <Radio
                                        checked={selectedValue === 'a'}
                                        onChange={handleChange}
                                        value="a"
                                        color="primary"
                                        name="radio-button-demo"
                                        inputProps={{ 'aria-label': 'A' }}
                                    />
                                </div>
                            </div>
                        </div> 
                        <div className="business-detail-input-content">
                            <SmallInput label="Enter GST" placeholder="Enter GST" type="text"/>
                        </div> 
                        <div className="business-detail-input-content">
                            <SmallInput label="Pancard - Number (Shop / Personal) *" placeholder={"Enter 16 digit pan number"}type="text"/>
                        </div> 
                        <div className="add-tags">
                            <div className="chips">
                                {chips.map(val => {
                                    return (
                                        <Chip label={val} onDelete={() => {
                                            let temp = chips;
                                            temp  = temp.filter(tem => tem != val);
                                            setChips(temp);
                                        }} color="primary" variant="outlined" />
                                    );
                                })}
                            </div>
                            <input 
                                style={{textIndent: chips.length * 80 + 10}}
                                type="text" 
                                placeholder="Please Mention category if not listed above" 
                                onChange={(e) => setValue(e.target.value)}
                                value={value}
                                onKeyDown={onHandleChange}
                            />
                        </div>     
                    </div>
                </div>
            </Paper>
        </div>
    )
}
export default BusinessDetail;