import * as actionTypes from './actionTypes';
import axios from 'axios';

export const getProducts = (catId,subCatId) => dispatch => {
    axios({
        method: "get",
        url: `/products?categoryId=${catId}&subCategoryId=${subCatId}`
    }).then(res => { 
        dispatch({
            type: actionTypes.SET_PRODUCTS,
            payload: res.data.productDetailsList
        })
    }).catch(err => {
        console.log(err);
    })
}


export const getMyProducts = () => dispatch => {
    axios({
        method: "get",
        url: `/myProducts`
    }).then(res => { 
        dispatch({
            type: actionTypes.SET_MY_PRODUCTS,
            payload: res.data.sellerProdDetailsList
        })
    }).catch(err => {
        console.log(err);
    })
}