import React from 'react'
import './specification.css';
import Form from '../../../utils/inputs/Input';

const Specification = (props) => {
    return (
        <div className="spec-container">
            <div className="head">
                <h5>Specification {props.index + 1}</h5>
                {props.index != 0 && <button onClick={() => props.remove()} className="btn-text">Remove</button>}
            </div>
            <div className="container-one">
                <Form 
                    displayName="Title" 
                    value={props.value.title}
                    onChange={(value) => props.onTitleChange(value)} 
                />
            </div>
            <div className="container-one">
                <Form 
                    displayName="Description" 
                    value={props.value.desc}
                    onChange={(value) => props.onDescChange(value)} 
                />
            </div>
        </div>
    );
}

export default Specification;