import React from 'react'
import './variantimages.css';
import { Divider } from '@material-ui/core';
import SubVariantImage from './subvariantimage/SubVariantImage';
import {connect} from 'react-redux' 

const VariantImages = (props) => {
    const [variantsTable,setVariantsTable] = React.useState([]); 
    React.useEffect(() => {
        setVariantsTable(props.variantsTable); 
    });  
    return (
        <div className="variant-upload-img">
            <h5>Variants</h5>
            {variantsTable.map((variant, index) => {
                return (
                    <>
                        <Divider />
                        <SubVariantImage 
                            state={props.state} 
                            index={index}  
                            name={`${variant.variant1} - ${variant.variant2 ? variant.variant2 : ""}`} /> 
                    </>
                )
            })}
        </div>
    );
}
const mapStateToProps = state => ({
    variantsTable: state.productConf.variantsTable
});
export default connect(mapStateToProps)(VariantImages); 