import React from 'react';
import {connect} from 'react-redux';
import PropModal from '../utils/propmodal/PropModal';
import SignIn from './signin/SignIn';
import OtpLogin from './optlogin/OtpLogin';

const Auth = (props) => {
    const [state,setState]  = React.useState("login-email")
    return (
        <div>
            <PropModal open={props.showLoginForm} onClose={() => {}}>
                {state == "login-email" && <SignIn  otpLogin={() => setState("otp-login")}/>}
                {state == "otp-login" && <OtpLogin signIn={() => setState("login-email")}  />}
            </PropModal>
        </div>
    )
}
const mapStateToProps = state => ({
    showLoginForm: state.appReducer.showLoginForm
})                                                                                         
export default connect(mapStateToProps)(Auth);