import * as actionTypes from './actionTypes'

const initialState = {
  category: false, 
  catMap: false,
  showLoginForm: false,
  showSnackBar: false
}

const reducer =  (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_CATEGORY:
      return {...state,category: action.payload}
    case actionTypes.SET_SELLER_CATEGORY: 
      return {...state,catMap: action.payload}
    case actionTypes.SHOW_LOGIN_FORM:
      return {...state,showLoginForm: action.payload}
    case actionTypes.SHOW_SNACK_BAR:
      return {...state,showSnackBar: action.payload}
    default:
      return state
  }
}

export default reducer;