import React from 'react'
import './index.css';

const Input = (props) => {
    return (
        <div className="form-group">
            <label htmlFor="input">{props.displayName ? props.displayName : "" }</label>
            <div>
                <select 
                    type="text" 
                    className="form-control select" 
                    placeholder={props.displayName}
                    onChange={(e) => props.onChange(e.target.value)}
                    value={props.value}
                >
                    {props.option.map(val => {
                        return <option className="one" value={val.id}>{val.brandName}</option>
                    })}
                </select>  
            </div>
        </div>
    );
}
export default Input;