import React from 'react';
import './orders.css';
import {data} from '../../data';
import AppBar from '../../components/utils/appbar/AppBar';
import SearchBar from '../../components/utils/searchbar/SearchBar';
import OrderList from '../../components/orders/orderlist/OrderList';
import TopBar from '../../components/utils/topbar/TopBar';
import Backbar from '../../components/utils/backbar/Backbar';
import OrderDetails from '../../components/orders/orderdetails/OrderDetails';
import {getOrders} from './action';
import {connect} from 'react-redux';

const Orders = (props) => {
    const [activeRoute, setRoute] = React.useState(0);
    const [activeIndex,setIndex] = React.useState(0);
    const [orderIndex,setOrderIndex] = React.useState(0);
    React.useEffect(() => {
        if(!props.orders) {
            props.getOrders();
        }
    },[])
    const routes = [
        {
            name: 'Orders',
            func: () => setRoute(0)
        }, {
            name: "Order",
            func: (index=false) => {
                index && setOrderIndex(index)
                setRoute(1)
            }
        } 
    ];
    const changeIndex = (index) => {
        setIndex(index);
    }
    const components = [
        {
            component: (
                <>
                    <TopBar head="Orders" />
                    <AppBar 
                        routes={routes[0]} 
                        index={activeIndex} 
                        tabs={true}
                        changeIndex={changeIndex} 
                        items={["In Process(33)","Delivered(0)","Cancelled(0)","Returned (0)","Requested (0)","Subscription (0)"]} 
                        btn={false} /> 
                    <SearchBar 
                        search={true} 
                        head={"Filter"}
                    />
                    <OrderList route={routes[1]} orders={props.orders} />
                </>
            ),
        } , { 
            component: (
                <>
                    <TopBar head="Order" />
                    <Backbar name="Order Details" click={() => setRoute(0)}/>
                    <OrderDetails 
                        data={props.orders[orderIndex]} 
                    />
                </>
            ),
        }  
    ]
    return (
        <div className="main-container">
            {components[activeRoute].component}
        </div>
    );
}
const mapStateToProps = state => ({
    orders: state.orders.orders
})
export default connect(mapStateToProps,{getOrders})(Orders);