import React from 'react'
import PropModal from '../../../utils/propmodal/PropModal';
import Switch from '@material-ui/core/Switch';
import './editstockprice.css';

const EditStockPrice = (props) => {
    const [state, setState] = React.useState({
        checkedA: true, 
    });
    const handleChange = (event) => {
        setState({ ...state, [event.target.name]: event.target.checked });
    };
    return  (
        <div className="edit-stock-price">
            <PropModal open={props.open} onClose={props.onClose}>
                <div className="edit-stock-price-container">
                    <h1>Edit Stock and Price</h1>
                    <div className="e-s-p-content">
                        <label htmlFor="">Edit Stock</label>
                        <input type="number"/>
                    </div>
                    <div className="e-s-p-content">
                        <label htmlFor="">Edit Price</label>
                        <input type="number"/>
                    </div>
                    <div className="e-s-p-content">
                        <label htmlFor="">Availability</label>
                        <div>
                            <Switch
                                checked={state.checkedA}
                                onChange={handleChange}
                                name="checkedA"
                                color="primary"
                                inputProps={{ 'aria-label': 'success checkbox' }}
                            />
                        </div>
                    </div>
                </div>
            </PropModal>
        </div>
    );
}
export default EditStockPrice;