import * as actionTypes from './actionTypes';

let initialState = {
    orders: false, 
}  
const reducer =  (state = initialState, action) => {
    switch (action.type) { 
        case actionTypes.SET_ORDERS:
            return {...state,orders: action.payload}
        case actionTypes.SET_INVOICE:
            let arr = state.orders;
            arr = arr.filter(order => {
                if(order.orderId == action.payload.id) {
                    order.invoicePath = action.payload.path;
                    return order;
                } else {
                    return order;
                }
            });
            return {...state,orders: arr}
        default:
            return state
    }
}
  
export default reducer;