import React from 'react';
import './notification.css';

import TopBar from '../../components/utils/topbar/TopBar';
import SearchBar from '../../components/utils/searchbar/SearchBar';
import NotificationContent from '../../components/notification/NotificationContent';

const Payment = () => {
    const [activeRoute, setRoute] = React.useState(0); 
    const routes = [
        {
            name: 'Notification',
            func: () => setRoute(0)
        } 
    ]; 
    const components = [
        {
            component: (
                <>
                    <TopBar head="Notification" />
                    <SearchBar 
                        search={true}
                    />
                    <NotificationContent />
                </>
            ),
        } 
    ]
    return (
        <div className="main-container">
            {components[activeRoute].component}
        </div>
    );
}

export default Payment;