import * as actionTypes from './actionTypes';
import axios from 'axios'; 

export const getCategory = () => dispatch => {
    axios({
        method: "get",
        notURL: true,
        url: "https://35.225.50.138:9090/category"
    }).then((res) => { 
        dispatch({
            type: actionTypes.SET_CATEGORY,
            payload: res.data.categoryList
        })
    }).catch(err => {
        console.log(err);
    })    
    
} 
export const getSellerCategory = () => dispatch => {
    axios({
        method: "get", 
        url: "/sellerCategory"
    }).then((res) => { 
        dispatch({
            type: actionTypes.SET_SELLER_CATEGORY,
            payload: res.data.catMap
        }) 
    }).catch(err => {
        console.log(err);
    })    
    
}

export const showLoginForm = (data) => dispatch => {
    dispatch({
        type: actionTypes.SHOW_LOGIN_FORM,
        payload: data
    })
}
export const showSnackbar = (data) => dispatch => {
    dispatch({
        type: actionTypes.SHOW_SNACK_BAR,
        payload: data
    })
}