import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import Accordion from '@material-ui/core/Accordion';
import backSvg from '../../../../assets/icons/back-close.svg';
import './product.css';
import Divider from '@material-ui/core/Divider';
import Variant from './variant/Variant'; 

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    margin: "10px 0px",
    boxShadow: "none"
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  shadowNone: {
    boxShadow: "none",
    border: '1px solid rgba(0,0,0,0.1)'
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
}));

const Product = (props) => {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);
  const [variants,setVariants] = React.useState([]) 

  React.useEffect(() => {
    if(props.reset) {
        setVariants([]);    
    }
  },[props.reset])
  return ( 
    <div className={classes.root}>
        <Accordion expanded={expanded} className={classes.shadowNone} onChange={() => setExpanded(!expanded)}>
            <AccordionSummary 
                aria-controls="panel1bh-content"
                id="panel1bh-header"
            >
                <div className="add-existing-product-header"> 
                    <div className="product-content-left">
                        <div className="product-header-img">
                            <img src={props.product.productResources[0] && props.product.productResources[0].resourceURL} alt=""/>
                        </div>
                        <div className="product-header-head">
                            <h1>{props.product.productName}</h1>
                            <div className="product-header-div d-flex a-i-c">
                                <p>Product ID: <span>{props.product.productId}</span></p>
                                <p>Brand Name: <span>{props.product.brandName}</span></p>
                            </div>
                        </div>
                    </div>
                    {
                        expanded 
                            ?
                        <button className="btn-outline button-accordian">
                            Hide Variants
                            <img src={backSvg} className="expansion-icon-close"  />
                        </button> 
                            :
                        <button className="btn-outline button-accordian">
                            Show Variants
                            <img src={backSvg} className="expansion-icon"  />
                        </button> 
                    }
                </div>
                <Divider />
            </AccordionSummary>
            <AccordionDetails>
                <div className="add-existing-product-content">
                    {props.product.variantDetailsList.length > 0
                        ?
                    <>
                        {props.product.variantDetailsList.map(variant => {
                            return (
                                <>
                                    <Variant 
                                        variant={variant}  
                                        addVariant={(value) => {
                                            let arr = variants;
                                            variants.push(value);
                                            setVariants([...arr])
                                        }}
                                        reset={props.reset}
                                        removeVariant={(id) => {
                                            let arr = variants; 
                                            arr = arr.filter(val => val.id != id)
                                            setVariants([...arr]);
                                        }}
                                    />
                                    <Divider />
                                </>
                            )
                        })}
                    </>
                        :
                    <div className="text-center font-13">Variants Not Found</div>
                    }
                    {variants.length > 0 && <div className="add-variant-store-content">
                        <h2>({variants.length}) Variants Selected</h2>
                        {props.loading ?
                        <button className="btn-blue">Adding....</button>
                            :
                        <button className="btn-blue"
                            onClick={() => props.addToStore({
                                variants,
                                productId: props.product.productId  
                            })}
                        >Add To Store</button>}
                    </div>}
                </div>
            </AccordionDetails>
        </Accordion> 
    </div>
  );
}

export default Product;