import React from 'react'
import './varianttable.css';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import eye from '../../../../assets/icons/eye.svg'
import del from '../../../../assets/icons/delete-.svg'
import {connect} from 'react-redux';
import {setVariantsTable} from '../../../../containers/productconf/actions';

const VariantTable = (props) => {
    const [table,setTable] = React.useState(props.table);

    React.useEffect(() => {
        if(props.state) { 
            props.setVariantsTable([...table]); 
        }
        if(props.variantsTable) { 
            setTable([...props.variantsTable]); 
        } 
        if(props.table.length > 0) {
            setTable(props.table);
        }
    },[props.data,props.state,props.table]);
    
    const onChangeQuantity = (e,i) => {
        let tab = table;
        tab[i].quantity = e;
        setTable([...tab]);
    }
    const onChangeYourPrice = (e,i) => {
        let tab = table;
        tab[i].yourPrice = e;
        setTable([...tab]);
    }
    const onChangeMrp = (e,i) => {
        let tab = table;
        tab[i].mrp = e;
        setTable([...tab]);
    }
    const onChangeInStock = (e,i) => {
        let tab = table;
        tab[i].inStock = e;
        setTable([...tab]);
    }
    return (
        <div className="v-table">
            <h4>Variants ({Object.keys(table).length})</h4>
            <TableContainer>
                <Table aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            {Object.keys(props.variants).map(variant => {
                                return (
                                    <TableCell align="center">{props.variants[variant].variantName}</TableCell>
                                )
                            })}
                            <TableCell align="center">Quantity</TableCell>
                            <TableCell align="center">MRP</TableCell>
                            <TableCell align="center">Your Price</TableCell>
                            <TableCell align="center">Variant Id</TableCell>
                            <TableCell align="center">SKU ID</TableCell>
                            <TableCell align="center">In Stock</TableCell>
                            <TableCell align="center">Action</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody> 
                        {table.map((content,i) => { 
                            return (
                                <TableRow key={i}> 
                                    <TableCell align="center">{content.variant1}</TableCell>
                                    {content.variant2 && <TableCell align="center">{content.variant2}</TableCell> }
                                    <TableCell align="center">
                                        <input
                                            type="number"
                                            value={content.quantity}
                                            className="form-table-input"
                                            onChange={(e) => onChangeQuantity(e.target.value,i)}
                                        /> 
                                    </TableCell>
                                    <TableCell align="center">
                                        <input
                                            type="number"
                                            value={content.mrp}
                                            className="form-table-input"
                                            onChange={(e) => onChangeMrp(e.target.value,i)}
                                        />
                                    </TableCell>
                                    <TableCell align="center">
                                        <input
                                            type="number"
                                            value={content.yourPrice}
                                            key={i}
                                            className="form-table-input"
                                            onChange={(e) => onChangeYourPrice(e.target.value,i)}
                                        /> 
                                    </TableCell>
                                    <TableCell align="center">{content.vId}</TableCell>
                                    <TableCell align="center">{content.pId}</TableCell>
                                    <TableCell align="center">
                                        <select 
                                            className="form-table-select" 
                                            value={content.inStock}
                                            onChange={(e) => onChangeInStock(e.target.value,i)}
                                        >
                                            <option value={true}>Yes</option>
                                            <option value={false}>No</option>
                                        </select>
                                    </TableCell>
                                    <TableCell>
                                        <div className="d-flex a-i-c j-c-c">
                                            <img src={eye} alt="eye" width="20px" style={{marginRight: "5px"}} />
                                            <img 
                                                src={del} 
                                                alt="eye" 
                                                width="20px" 
                                                style={{marginLeft: "5px"}} 
                                                className="hover"
                                                onClick={() => {
                                                    let tab = table; 
                                                    tab = tab.filter((val,index) => i != index)
                                                    setTable([...tab]);
                                                }}
                                            />
                                        </div>
                                    </TableCell>
                                </TableRow>
                            ) 
                        })}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
}
const mapStateToProps = state => ({
    variantsTable: state.productConf.variantsTable
});
export default connect(mapStateToProps,{setVariantsTable})(VariantTable);