import React from 'react';  
import './App.css';
import {
  BrowserRouter as Router,
  Switch,
  Route 
} from "react-router-dom";
import axios from 'axios';
import {connect} from 'react-redux';

import Navbar from './components/navbar/Navbar';
import MyStore from './containers/mystore/MyStore';
import Orders from './containers/orders/Orders';
import Payment from './containers/payment/Payment';
import Notification from './containers/notification/Notification';
import Dashboard from './containers/dashboard/Dashboard';
import ProductConf from './containers/productconf/ProductConf';
import MyAccount from './containers/myaccount/MyAccount';
import PrivateRoute from './routes/PrivateRoute';
import Auth from './components/auth/Auth';
import SnackBar from './components/utils/snackbar/SnackBar';

import {basicAuth, url} from './config/index';
import {getCategory, getSellerCategory} from './containers/app/action';

import Amplify from 'aws-amplify';
import awsconfig from './aws-exports';
Amplify.configure(awsconfig);

axios.interceptors.request.use(async (config) => {
  if(config.notURL) {
    config = {
      ...config,
      ...basicAuth,
    }
  } else {
    config = {
      ...config,
      ...basicAuth,
      url: `${url}${config.url}`
    }
  }
  config.headers = {
    ...config.headers,
    ...basicAuth
  } 
  return config;
}); 
axios.interceptors.response.use((response) => { 
  return response;
}, (error) => { 
  if(error.response && (error.response.data.errorcode == "008" || error.response.status == "401" || error.response.data.errorcode == " LG06")) {
    localStorage.clear();  
    window.location.reload();
  }
  if(error.response && error.response.data.errorcode == "LG02") {
    axios.get(url + "/signout")
      .then((res) => {})
      .catch(err => {})
  } 
  return Promise.reject(error);
}); 

class App extends React.Component { 
  componentWillMount = () => {
    if(localStorage.isUserLoggedIn && localStorage.getItem("isUserLoggedIn")){
      this.props.getCategory();
      this.props.getSellerCategory();
    }
  }
  logout = () => { 
    axios
      .get("/signout")
      .then((res) => {
        localStorage.clear(); 
        window.location.reload();
      })
      .catch(err => {
        localStorage.clear(); 
        window.location.reload();
      })  
  }
  render() {
    return ( 
        <Router>
          <div className="global-container">
            <Navbar logout={this.logout} />
            <Auth />
            <SnackBar />
            <div className="content-container">
              <Switch> 
                <PrivateRoute exact path="/" component={Dashboard} /> 
                <PrivateRoute exact path="/my-store" component={MyStore} /> 
                <PrivateRoute exact path="/product-configure/:categoryId/:subCategoryId" component={ProductConf} /> 
                <PrivateRoute exact path="/orders" component={Orders} />
                <PrivateRoute exact path="/payment" component={Payment} />
                <PrivateRoute exact path="/notification" component={Notification} />
                <PrivateRoute exact path="/my-account" component={MyAccount} />
                <PrivateRoute path="*" component={() => <div className="not-found">404 Page Not Found</div>} /> 
              </Switch> 
            </div>
          </div>
        </Router> 
    );
  }
}
export default connect(null, {getCategory,getSellerCategory})(App);
