import React from 'react'
import './mainimages.css';
import add_photo from '../../../../assets/icons/add_photo.svg';
import CropImage from '../cropImage/CropImage';
import PropModal from '../../../utils/propmodal/PropModal';
import {connect} from 'react-redux'
import {setProductImage} from '../../../../containers/productconf/actions';
import { Storage } from 'aws-amplify'; 
import AlertDailog from '../../../utils/alertDailog/AlertDailog';

const MainImages = (props) => {
    const [img,setImg] = React.useState([]);
    const [keys, setKeys] = React.useState([]);
    const [crop,setCrop] = React.useState(false);
    const [cropImg,setCropImg] = React.useState(false);
    const [alert,showAlert] = React.useState(false);
    const [delIndex,setDelIndex] = React.useState(false);


    React.useEffect(() => {
        if(props.state) { 
            props.setProductImage([...img]); 
        }
        if(props.productImages) { 
            setImg([...props.productImages]); 
        } 
    },[props.data,props.state,props.validate]);
    
    const setStateImg = (e) => { 
        setCropImg(e.target.files[0]); 
        setCrop(true)
    } 
    const addImg = (url,key) => {
        let images = img;
        let temp = keys;
        temp.push(key);
        images.push(url);
        setImg([...images]);
        setKeys([...temp]);
        setCrop(false);
    }
    const removeImageS3 = () => {
        showAlert(false);
        let images = img;
        images = images.filter(val => val != img[delIndex]);
        setImg(images);
        const key = keys[delIndex]; 
        Storage.remove(key)
            .then(result => console.log(result))
            .catch(err => console.log(err));
    } 
    return  (
        <div className="main-image">
            <h5>main image</h5> 
            <AlertDailog 
                open={alert}
                close={() => showAlert(false)}
                msg={alert}
                onAgree={() => removeImageS3()}
            />
            {cropImg && crop && 
                <div>
                    <PropModal open={true} onClose={() => {
                        setCropImg(false)
                        setCrop(false)
                    }}>
                        {cropImg && <CropImage src={URL.createObjectURL(cropImg)} image={cropImg} onDone={((url,key) => addImg(url,key)) } />}
                    </PropModal>
                </div>}
            <div className="main-image-container">
                {img && img.map((val,index) => {
                    return (
                        <div className="main-img-content main-img-content-border"> 
                            <img src={val} className="main-img-content-img"/>
                            <span
                                onClick={() => { 
                                    setDelIndex(index);
                                    showAlert("Are you sure? you want to delete this image..")
                                }}
                            >x</span>
                        </div>
                    );
                })}
                <div className="main-img-content">
                    <img src={add_photo} alt="Add Photos" className="add-image-icon" />
                    <p>Click to Upload</p>
                    <input 
                        type="file"  
                        onChange={setStateImg}
                        className="main-multi-img-input"
                    />
                </div>
            </div>
        </div>
    );
}
const mapStateToProps = state => ({
    productImages: state.productConf.productImages
});
export default connect(mapStateToProps, {setProductImage})(MainImages);

