import React from 'react'
import './topbar.css';
import backBtn from '../../../assets/icons/back-arrow.svg';
import notification from '../../../assets/icons/notifications.svg'
import chatbox from '../../../assets/icons/chatbox.svg'
const TopBar = (props) => {
    return (
        <div className="top-bar-right">
            <div className="d-flex a-i-c cursor">
                {props.back && <img src={backBtn} onClick={props.backClick} className="back-btn" alt="back-btn" />}
                <h5>{props.head}</h5>
            </div>
            <div className="nav-btns">
                <img src={notification} />
                <img src={chatbox} />
                <p className="nav-btn">English</p>
            </div>
            <div className="mobile-view-nav">
                {props.btn && props.button}
            </div>
        </div>
    );
}
export default TopBar;