import React from 'react'; 
import Snackbar from '@material-ui/core/Snackbar'; 
import {showSnackbar} from '../../../containers/app/action';
import close from '../../../assets/icons/close.svg';
import {connect} from 'react-redux';

const SnackBar = (props) =>  {
    const [open, setOpen] = React.useState(props.showSnackBar);
    React.useEffect(() => {
        setOpen(props.showSnackBar)
    },[props.showSnackBar])
    const handleClose = (event, reason) => {
        if (reason === 'clickaway') {
            return;
        } 
        props.showSnackbar(false);
    }; 
    return (
        <>
        {props.showSnackBar && <div> 
            <Snackbar
                anchorOrigin={{
                    vertical: 'bottom',
                    horizontal: 'right',
                }}
                open={open} 
                onClose={handleClose}
                message={props.showSnackBar}
                action={
                    <React.Fragment> 
                        <img src={close} onClick={handleClose} className="hover" style={{filter: 'invert(100%)'}} />
                    </React.Fragment>
                }
            />
        </div>}
        </>
    );
}
const mapStateToProps = state => ({
    showSnackBar: state.appReducer.showSnackBar
})
export default connect(mapStateToProps,{showSnackbar})(SnackBar);