import React from 'react'
import logo from '../../assets/img/logo.png';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import './productconf.css'

import BasicInfo from '../../components/productconf/basicinfo/BasicInfo';
import Variants from '../../components/productconf/variants/Variants';
import Images from '../../components/productconf/images/Images';
import DetailedInfo from '../../components/productconf/detailedinfo/DetailedInfo';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import AlertError from '../../components/utils/alertError/AlertError';
import axios from 'axios';
import {showSnackbar} from '../app/action';
import BackDropLoader from '../../components/utils/backdrop/BackDropLoder';

const ProductConf = (props) => {
    const [tab, setTab] = React.useState("basic-info"); 
    const [state, setState] = React.useState(false); 
    const [category,setCategory] = React.useState(false);
    const [subCategory,setSubCategory] = React.useState(false);
    const [disabledTabs,setDisabledTabs] = React.useState([false,true,true,false]);
    const [imgErr, setImgErr] = React.useState(false);
    const [dErr,setDerr] = React.useState(false);
    const [validated,setValidated] = React.useState([false,false,false,false]);
    const [loading,setLoading] = React.useState(false);
    const [err,setErr] = React.useState(false);
    const [productConf,setProductConf] = React.useState(props.productConf);
    const [detailedInfo,setDetailedInfo] = React.useState([]);


    React.useEffect(() => {
        let cat = props.category && props.category.filter(val => {
            return val.id == props.match.params.categoryId; 
        })
        let sub = cat && cat[0].subCategoryList.filter(val => { 
            return val.id == props.match.params.subCategoryId
        }); 
        if(props.category) {
            if(cat.length > 0 && sub.length > 0) { 
                setCategory(cat[0]);
                setSubCategory(sub[0]);
            } else {
                props.showSnackbar("Category Id Not Found");
                props.history.push("/my-store");
            }
        }  
        if(props.productConf) { 
            setProductConf({...props.productConf})
            props.productConf.detailedInfo.length > 0 && setDetailedInfo([...props.productConf.detailedInfo])
        }
    },[props.category,props.productConf]); 

    const handleChange = (event, newValue) => {
        setState(true); 
        setTimeout(() => {
            setState(false);
            setTab(newValue); 
        },200);
    };  

    const enableVariant = () => {
        let arr = disabledTabs;
        arr[1] = false;
        let temp = validated;
        temp[0] = true;
        setDisabledTabs([...arr]);
        setValidated([...temp]);
        setState(true);
        setTimeout(() => {
            setState(false);
            setTab("variants"); 
        },200);
    }
    const enableAddImage = () => {
        setState(true);
        let arr = disabledTabs;
        arr[2] = false;
        let temp = validated;
        temp[1] = true;
        setValidated([...temp]);
        setDisabledTabs([...arr]);
        setTimeout(() => {
            setState(false);
            setTab("images"); 
        },200);
    }
    const validateImage = () => {
        let value = true;
        setState(true);
        setImgErr(false);
        setTimeout(() => {
            setState(false); 
        },200);
        if(props.productConf.productImages.length == 0) {
            value = false;
            setImgErr("Atleast 1 image should be there for a Product")
        }
        props.productConf.variantsTable.forEach((variant,index) => { 
            if(props.productConf.variantImages[index] && props.productConf.variantImages[index].length == 0) {
                value = false;
                setImgErr("Atleast 1 image should be there for each variant");
            } 
        });
        !value && setTab("images");
        return value;
    }
    const validateDetailedInfo = () => {
        let value = true;
        detailedInfo.forEach((val, index) => {
            if(val.title == "" || val.desc =="") {
                value = false;
                setDerr(`Specification ${index+1} fill all the fields`);
            }
        });
        !value && setTab("detailed-info");
        return value;
    }
    const reciveAllData = async () => {
        setState(true);
        setErr(false);
        await setTimeout(() => {
            setState(false); 
            onProductSubmit();
        },200)
    } 
    const onProductSubmit = () => { 
        if(validated[0] && validated[1] && validateImage() && validateDetailedInfo()) {  
            setLoading(true);    
            let basicInfo = productConf.basicInfo; 
            let productResources = productConf.productImages.map((url,index) => {
                return {
                    resourceType: "image",
                    resourceURL: url,
                    orderRender: index + 1
                }
            });
            let variantList = productConf.variantsTable.map((variant,index) => {
                return {
                    productId: 1,
                    variantName: variant.variant1,
                    variantDescription: "Variant Description goes here",
                    mrp: variant.mrp,
                    variantResourceList:  productConf.variantImages[index].map((url,i) => {
                        return {
                            resourceType: "image",
                            resourceURL: url,
                            orderRender: i + 1
                        }
                    })
                }
            });
            let productSpecification = {};
            
            detailedInfo.forEach(val => { 
                productSpecification[val.title] = val.desc
            })  

            let data = {
                addProduct: {
                    productName: basicInfo.name,
                    productDescription: basicInfo.description,
                    productKeyFeatures: basicInfo.spec,
                    productSpecification: productSpecification,
                    categoryId: category.id,
                    subCategoryId: subCategory.id,
                    brandId: basicInfo.brandId,
                    brandName: basicInfo.brandName,
                    productResources,
                },
                addVariantList: variantList,
            }  
            let json = JSON.stringify(data); 
            axios({
                method: "POST",
                url: "/addProduct",
                data: json,
                headers: {
                    "Content-Type": "application/json"
                }
            }).then((res) => {
                setLoading(false);
                props.showSnackbar("Product Added Successfully");
                window.location.replace("/my-store")
            }).catch(err => {
                setLoading(false);
                props.showSnackbar("Something went wrong !Try Again");
            })
        } 
    };

    return (
        <div className="product-conf">
            {loading && <BackDropLoader />}
            {err.show && <AlertError title={err.title} msg={err.msg} onClose={() => setErr({...err,show: false}) }/>}
            <div className="product-conf-top-bar">
                <div className="product-conf-row-1">
                    <img src={logo} alt="logo" className="product-conf-img" onClick={() => props.history.push("/my-store")}/>
                    <button className="btn-blue" onClick={reciveAllData}>Submit Details</button>
                </div>
                <div className="product-conf-tabs">
                    <Tabs
                        value={tab}
                        indicatorColor="primary"
                        textColor="primary"
                        onChange={handleChange}
                        aria-label="disabled tabs example"
                    >
                        <Tab 
                            label="Basic Info"  
                            disabled={disabledTabs[0]}
                            style={{textTransform: "none"}} 
                            value="basic-info" />
                        <Tab 
                            label="Variants" 
                            disabled={disabledTabs[1]}
                            style={{textTransform: "none"}} 
                            value="variants" />
                        <Tab 
                            label="Images" 
                            disabled={disabledTabs[2]} 
                            style={{textTransform: "none"}} 
                            value="images" />
                        <Tab 
                            label="Detailed Info"
                            disabled={disabledTabs[3]}
                            style={{textTransform: "none"}} 
                            value="detailed-info"/>
                    </Tabs>
                </div>
            </div>
            <div className="product-conf-content">
                {tab == "basic-info" && 
                    <BasicInfo 
                        state={state} 
                        cat={category} 
                        enableVariant={enableVariant}
                        sub={subCategory} />}
                {tab == "variants" && 
                    <Variants 
                        state={state} 
                        cat={category} 
                        sub={subCategory}
                        enableVariant={enableAddImage} />}
                {tab == "images" && 
                    <Images 
                        state={state} 
                        cat={category} 
                        sub={subCategory} 
                        error={imgErr}/>}
                {tab == "detailed-info" && 
                    <DetailedInfo 
                        state={state} 
                        cat={category} 
                        error={dErr}
                        sub={subCategory}/>}
            </div>
        </div>
    )
}
const mapStateToProps = state => ({
    productConf: state.productConf,
    detailedInfo: state.productConf.detailedInfo,
    category: state.appReducer.category
});
export default withRouter(connect(mapStateToProps,{showSnackbar})(ProductConf));