import React from 'react';
import './addeproductlist.css';
import axios from 'axios';
import Product from './product/Product';
import AppLoader from '../../utils/appLoader/AppLoader';
import {connect} from 'react-redux'
import { showSnackbar} from '../../../containers/app/action';


const AddEProductlist = (props) => {
    const [products,setProducts] = React.useState([]);
    const [reset,setReset] = React.useState(false);
    const [loading,setLoading] = React.useState(false);
    React.useEffect(() => {
        if(props.products) {
            let arr = props.products.filter(val => {
                return val.productName.toLowerCase().includes(props.searchString.toLowerCase());
            }); 
            setProducts([...arr]);
        } 
    },[props.searchString,props.products]);
    
    // :SORTING FOR DROPDOWN
    React.useEffect(() => { 
        if(props.dropValue == 1 && props.myProductVariantId && props.products) {
            let arr = [];
            props.products.forEach(product => {
                let subArr = [];
                product.variantDetailsList.forEach(variant => {
                    if(!props.myProductVariantId.includes(variant.variantId)) {
                        subArr.push(variant);
                    }
                })
                arr.push({
                    ...product,
                    variantDetailsList: subArr
                });
            });
            console.log(arr);
            setProducts([...arr]);
        } else {
            setProducts(props.products)
        }
    },[props.dropValue])
    const addToStore = (value) => {
        let data = [];
        setLoading(true);
        value.variants.map(val => {
            data.push({
                productId: value.productId,
                variantId: val.id,
                sellerPrice: val.price,
                elocalsPrice: val.price,
                quantity: val.quantity,
                categoryId: props.catId,
                subCategoryId: props.subCatId
            });
        });
        axios({
            url:"/addSellerProduct",
            method: "post",
            data: JSON.stringify(data),
            headers: {
                "Content-Type": "application/json"
            }
        }).then(res => {
            setReset(true);
            setReset(false);
            setLoading(false);
            props.showSnackbar("Product Added to Store")
        }).catch(err => { 
            setLoading(false);
            props.showSnackbar("Could'nt add product! Try Again")
        }); 
    }
    return (
        <div className="add-existing-product container-one">
            <>
                {!props.products
                        ?
                    <AppLoader />
                        :
                    <>
                        {products.length > 0 
                            ?
                        <>
                            {products.map(val => <Product loading={loading} reset={reset} product={val} addToStore={addToStore} />)}
                        </>
                            :
                        <div className="text-center">No Product Found</div>
                        }
                    </>
                }
            </>
        </div>
    );
}
const mapStateToProps = state => ({
    myProductVariantId: state.mystore.myProductVariantId
});
export default connect(mapStateToProps,{showSnackbar})(AddEProductlist);