import React from 'react';
import Cropper from 'cropperjs'
import 'cropperjs/dist/cropper.min.css';
import './cropimage.css';
import { Storage } from 'aws-amplify'; 
import CircularProgress from '@material-ui/core/CircularProgress';
 
const CropImage = (props) => {
  const imageElement = React.createRef();
  const [cropedImage,setCropedImage] = React.useState(false);
  const [progress, setProgress] = React.useState(false);
  const [blob,setBlob] = React.useState(false);

  React.useEffect(() => {
    const cropper = new Cropper(imageElement.current, {
      zoomable: true,
      scalable: false,
      aspectRatio: 1,
      wheelZoomRatio:0.1,  
      ready: () => {
        cropper.zoom(-0.1);
      },
      crop: () => {
        const canvas = cropper.getCroppedCanvas();
        setCropedImage(canvas.toDataURL("image/png"))
        canvas.toBlob(function(blob) {
          setBlob(blob);
        }, "image/png", 0.90);
      }
    })
  });
  const handleUpload = () => {
    setProgress(true);
    setTimeout(() => {
      let temp = Date.now().toString();
      let fileName = temp + ".png"; 
      Storage.put(fileName,blob ,{ 
          ContentType: 'image/png'
      }).then((res) => {  
          Storage.get(fileName)
              .then(url => { 
                props.onDone(url.split("?")[0],res.key);
              })
              .catch(err => console.log(err));
      })
      .catch(err => { 
          setProgress(false);
      })
    },2000);
  } 
  return (
    <div className="crop-image"> 
      {progress && <div className="crop-image-loading-container">
        <CircularProgress />
        <span>Uploading.....</span>
      </div>}
      <div className="crop-image-container">
        <img src={props.src} ref={imageElement} alt="Source Image" className="src-image" />
      </div>
      <div className="cropped-image-container">
        {cropedImage &&
        <>
          <img src={cropedImage} className="cropped-img" />
          <button className="btn-blue" onClick={handleUpload}>Crop Image and Save</button>
        </>
        }
      </div>
    </div>
  );
}
export default CropImage;