import * as actionTypes from './actionTypes';
import axios from 'axios';

export const getOrders = () => dispatch => {
    axios({
        method: "get",
        url: "/getOrders",
    }).then(res => {
        dispatch({
            type: actionTypes.SET_ORDERS,
            payload: res.data.orderDetailsList
        })
    }).catch(err => {
        console.log(err);   
    })
}
export const setInvoice = (id,path) => dispatch => {
    console.log(id)
    console.log(path);
    dispatch({
        type: actionTypes.SET_INVOICE,
        payload: {
            id,path
        }
    })
}