import React from 'react';
import './variant.css';
import Checkbox from '@material-ui/core/Checkbox'; 
const Variant = (props) => {   
    const [checked,setChecked] = React.useState(false);
    const [formValue,setFormValue] = React.useState({
        quantity: "",
        price: "",
    });
    const [formError,setFormError] = React.useState({
        quantity: false,
        price: false,
    });

    React.useEffect(() => {
        setChecked(false);
        setFormValue({price: "",quantity: ""})
    },[props.reset])

    const validate = () => {
        let value = true;
        let err = {quantity: false,price: false}
        setFormError({...err});
        if(formValue.price == "" || formValue.price < 0) {
            err.price = "Price Can't be empty";
            value = false;
        }
        if(formValue.quantity == "" || formValue.quantity < 0) {
           err.quantity = "Quantity Can't be empty";
            value = false;
        }
        setFormError({...err});
        return value;
    }

    const handleChange = () => {
        if(checked) {
            setChecked(false)
            props.removeVariant(props.variant.variantId);
        } else {
            if(validate()){ 
                setChecked(true)
                props.addVariant({
                    id: props.variant.variantId,
                    quantity: formValue.quantity,
                    price: formValue.price
                });
            }
        }
    };
    return (
        <div className="variant-content">
            <div className="variant-col-1">
                <Checkbox
                    checked={checked}
                    color={"primary"}
                    onChange={handleChange}
                    inputProps={{ 'aria-label': 'primary checkbox' }}
                />
                <img src={props.variant.variantResources[0] && props.variant.variantResources[0].resourceURL} alt="variant" className="variant-img" />
            </div> 
            <div className="variant-col-2">
                <div className="variant-row-1">
                    <h5>{props.variant.variantName}</h5>
                </div>
                <div className="variant-row-2">
                    <div className="variant-row-2-content">
                        <div className="variant-spec">
                            <p className="variant-key">MRP</p>
                            <p className="variant-value">Rs {props.variant.mrp}</p>
                        </div>
                        <div className="variant-spec">
                            <p className="variant-key">Variant Id</p>
                            <p className="variant-value">{props.variant.variantId}</p>
                        </div> 
                    </div> 
                </div> 
            </div> 
            {!checked &&
            <div className="variant-col-3">
                <div className="variant-form-group">
                    <label htmlFor="quantity">Quantity</label>
                    <div>
                        <input  
                            type="number" 
                            className="variant-form-control" 
                            value={formValue.quantity} 
                            onChange={(e) => setFormValue({...formValue, quantity: e.target.value})} 
                        />
                        {formError.quantity && <span className="text-error">{formError.quantity}</span>}
                    </div>
                </div>
                <div className="variant-form-group">
                    <label htmlFor="quantity">Your Price</label>
                    <div>
                        <input  
                            type="number" 
                            className="variant-form-control" 
                            value={formValue.price} 
                            onChange={(e) => setFormValue({...formValue, price: e.target.value})} 
                        />
                        {formError.price && <span className="text-error">{formError.price}</span>}
                    </div>
                </div>
            </div>}
        </div>
    );
}

export default Variant;