import React from 'react';
import './appbar.css'; 
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
 
const AppBar = (props) => {
    return (
        <div className="content-nav-bar">
            <div className="app-bar-tabs">{props.tabs ? <Tabs 
                value={props.index}
                variant="scrollable"
                indicatorColor="primary"
                textColor="primary"
            >
                {props.items.map((val,i) => {
                    return (
                        <Tab 
                            value={i}
                            style={{textTransform: "none"}}
                            onClick={() => props.changeIndex(i)} label={val} />
                    );
                })} 
            </Tabs>: <div>&nbsp;</div>}
            </div>
            <div className="desktop-view-nav">
                {props.btn && props.button}
            </div>
        </div>
    );
}

export default AppBar;