import React from 'react'
import './basicinfo.css';
import { Paper, Divider } from '@material-ui/core';
import Form from '../../utils/inputs/Input';
import TextArea from '../../utils/inputs/TextArea';
import {connect} from 'react-redux';
import {setBasicInfo} from '../../../containers/productconf/actions';
import SelectList from '../../utils/inputs/SelectList';

const BasicInfo = (props) => {
    const [specInputs, setSpecInputs] = React.useState([""]);
    const [basicData, setBasicData] = React.useState({
        name: "",
        mrp: "",
        yourPrice: "",
        shippingCharge10_30: "",
        shippingCharge30_60: "",
        description: "",
        brandId: 0,
        brandName: "",
        spec: []
    }); 
    const [errorBasicData, setErrorBasicData] = React.useState({
        name: false,
        mrp: false,
        yourPrice: false,
        shippingCharge10_30: false,
        shippingCharge30_60: false,
        description: false,
        brandId: false,
        brandName: false
    }); 
    const [brandList,setBrandList] = React.useState([])
    const [specError,setSpecError] = React.useState([false]);

    React.useEffect(() => {
        if(props.state) { 
            props.setBasicInfo({...basicData,spec: specInputs}); 
        }
        if(props.basicInfo) { 
            setBasicData({...basicData,...props.basicInfo});
            setSpecInputs([...props.basicInfo.spec]);
        }
    },[props.state]); 

    React.useEffect(() => {
        if(props.sub) {
            setBrandList([...props.sub.brandList]);
            setBasicData({...basicData,brandId: props.sub.brandList[0].id, brandName: props.sub.brandList[0].brandName})
        }
    },[props.sub]);
    
    const validate = () => {
        let value = true; 
        let err = { name: false,mrp: false,yourPrice: false,
            shippingCharge10_30: false,shippingCharge30_60: false,
            description: false,brandId: false,brandName: false}
        let serr = specError.map((val) => {
            return false;
        });

        if(basicData.name == "") {
            value = false;
            err.name = "Product Name cannot be empty"
        }
        if(basicData.yourPrice == "") {
            value = false;
            err.yourPrice = "Your Price Name cannot be empty"
        } 
        if(basicData.mrp == "") {
            value = false;
            err.mrp = "MRP Name cannot be empty"
        }
        if(basicData.shippingCharge10_30 == "") {
            value = false;
            err.shippingCharge10_30 = "Shipping Charge Name cannot be empty"
        }
        if(basicData.shippingCharge30_60 == "") {
            value = false;
            err.shippingCharge30_60 = "Shipping Charge cannot be empty"
        }
        if(basicData.description == "") {
            value = false;
            err.description = "Product Description Name cannot be empty"
        }
        specInputs.forEach((spec,index) => {
            if(spec == "") {
                serr[index] = "Field cannot be empty";
                value = false;
            }
        });
        setSpecError([...serr]);
        setErrorBasicData({...errorBasicData,...err}); 
        return value;
    }
    const nextPage = () => {
        if(validate()) {
            props.enableVariant();
        }
    }
    return (
        <div className="basic-info">
            <Paper variant="outlined">
                <div className="basic-info-content container-one">
                    <div className="d-flex a-i-c j-c-b">
                        <p className="basic-conf-head">{props.cat && props.cat.categoryname}  {props.sub && props.sub.subCategoryName}</p>
                        <button className="btn-text" onClick={nextPage}>Next Page</button>
                    </div>
                    <Divider />
                    <h4>Basic Info</h4>
                    <div className="form-container">
                        <div className="form-content">
                            <SelectList
                                displayName="Brand Name" 
                                value={basicData.brandId}   
                                option={brandList}
                                onChange={(value) => { 
                                    let arr = brandList.filter(val => val.id == value); 
                                    setBasicData({...basicData, brandName: arr[0].brandName,brandId: value})
                                }}
                            />
                            <Form 
                                displayName="Product Name" 
                                value={basicData.name}
                                error={errorBasicData.name}
                                onChange={(value) => setBasicData({...basicData, name: value})} 
                            />
                            <Form 
                                displayName="MRP (INR) *" 
                                value={basicData.mrp}
                                type="number"
                                error={errorBasicData.mrp}
                                onChange={(value) => setBasicData({...basicData, mrp: value})}
                            />
                            <Form 
                                displayName="Your Price(INR) *" 
                                value={basicData.yourPrice}
                                type="number"
                                error={errorBasicData.yourPrice}
                                onChange={(value) => setBasicData({...basicData, yourPrice: value})}
                            />
                            <Form 
                                displayName="Shipping Charge (10-30km)" 
                                value={basicData.shippingCharge10_30}
                                error={errorBasicData.shippingCharge10_30}
                                onChange={(value) => setBasicData({...basicData, shippingCharge10_30: value})}
                            />
                            <Form 
                                displayName="Shipping Charge (30-60km)" 
                                value={basicData.shippingCharge30_60}
                                error={errorBasicData.shippingCharge30_60}
                                onChange={(value) => setBasicData({...basicData, shippingCharge30_60: value})}
                            /> 
                            {specInputs.map((val,i) => {
                                return (
                                    <Form 
                                        displayName={null} 
                                        removeIcon={i != 0 && true} 
                                        displayName={i==0 && "Key Specification"}
                                        onRemove={() => {
                                            let arr = specInputs.filter((val,index) => i != index)
                                            let err = specError.filter((val,index) => i != index);
                                            setSpecError(err);
                                            setSpecInputs(arr);
                                        }}
                                        value={specInputs[i]}
                                        error={specError[i]}
                                        onChange={(value) => {
                                            let data = specInputs;
                                            data[i] = value;
                                            setSpecInputs([...data]);
                                        }}
                                    />
                                );
                            })}
                            <div className="form-div">
                                <label htmlFor="none"></label>
                                <button className="btn-text"
                                    onClick={() => { 
                                        setSpecInputs([...specInputs,""]);
                                        setSpecError([...specError,false]);
                                    }}
                                >+ Add Specification</button>
                            </div>
                            <TextArea 
                                displayName="Description" 
                                value={basicData.description}
                                error={errorBasicData.description} 
                                onChange={(value) => setBasicData({...basicData, description: value})}
                            />
                        </div>
                    </div> 
                    <div className="margin-container">
                        &nbsp;
                    </div> 
                </div>
            </Paper>
        </div>
    )
}
const mapStateToProps = state => ({
    basicInfo: state.productConf.basicInfo
})
export default connect(mapStateToProps,{setBasicInfo})(BasicInfo);