import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(1),
    width: 200,
  },
}));

const Date = (props) => {
    const classes = useStyles();
    return (
        <div className="form-group">
            <label htmlFor="input">{props.displayName ? props.displayName : "" }</label>
            <div className="d-flex a-i-c flex-column">
                <form className="form-date-group" noValidate>
                    <TextField
                        id="date" 
                        type="date"
                        defaultValue="2017-05-24"
                        className={classes.textField}
                        InputLabelProps={{
                        shrink: true,
                        }}
                    />
                </form>
                {props.error && <span className="text-error">{props.error}</span>}
            </div>
            {props.removeIcon && <span className="remove-form-icon" onClick={props.onRemove}>x</span>}
        </div>
    )
}
export default Date;