import * as actionTypes from './actionTypes'

const initialState = {
  basicInfo: {
    spec: [""],
    name: "",
    mrp: 0,
    yourPrice: 0,
    shippingCharge10_30: "",
    shippingCharge30_60: "",
    description: "",
    brandId: 0,
    brandName: ""
  },
  variants: [{variantName: "color", tags: [],error: false}],
  variantsTable: [],
  detailedInfo: [{title: "",desc: ""}],
  productImages: [],
  variantImages: []
}

const reducer =  (state = initialState, action) => {
  switch (action.type) {
    case actionTypes.SET_BASIC_INFO:
      return { ...state, basicInfo: action.payload };
    case actionTypes.SET_DETAILED_INFO:  
      return { ...state, detailedInfo: action.payload}
    case actionTypes.ADD_DETAILED_INFO:  
      let arr = state.detailedInfo;
      arr.push({title: "",desc: ""})
      return { ...state, detailedInfo: [...arr]}
    case actionTypes.REMOVE_DETAILED_INFO:  
      let value = state.detailedInfo;
      value = value.filter((val,i) => i != action.payload)
      return { ...state, detailedInfo: [...value]};
    case actionTypes.SET_TITLE_VALUE:
      let val = state.detailedInfo;
      val[action.payload.index].title = action.payload.value;
      return { ...state, detailedInfo: [...val]};
    case actionTypes.SET_DESC_VALUE:
      let tmp = state.detailedInfo;
      tmp[action.payload.index].desc = action.payload.value;
      return { ...state, detailedInfo: [...tmp]};
    case actionTypes.SET_VARIANTS:
      return { ...state, variants: action.payload }
    case actionTypes.SET_VARIANTS_TABLE:
      return { ...state, variantsTable: action.payload }
    case actionTypes.SET_PRODUCT_IMAGE:
      return { ...state, productImages: action.payload }
    case actionTypes.SET_VARIANT_IMAGE:
      let temp = state.variantImages;
      temp[action.payload.index] = action.payload.data;
      return { ...state, variantImages: temp }
    default:
      return state
  }
}

export default reducer;