import React from 'react'
import './OtpLogin.css';
import axios from 'axios' 
import OtpVerify from '../otpverify/OtpVerify'; 

const OtpLogin = (props)=> {
    const [number,setNumber] = React.useState("");
    const [error,setError] = React.useState(false);
    const [loading,setloading] = React.useState(false);
    const [showOtp, setShowOtp] = React.useState(false);
    const [success, showSuccess] = React.useState(false);
    const [fail, showFail] = React.useState(false);

    const handleOtp= () => {
        setError(false);
        if(number.length != 10 || number == "") {
            setError("Enter valid phoneNumber")
        } else {
            setloading(true);
            axios({
                method:'get',
                url: '/sendOTP?login=true', 
                headers: { 
                    phoneNumber: "91" + number, 
                },
            }).then(res=>{  
                setloading(false); 
                if(res.data.errorcode == "LG05") { 
                    showFail(true);
                } else {
                    setShowOtp(true);
                }
            }).catch(err => { 
                setloading(false);  
                if(err.response) {
                    if(err.response.status == 400) {
                        setError(err.response.data.message);
                    } 
                    
                } else {
                    setError("Something went wrong try again")
                }
            });
        }
    }
    const resendOtp = () => {
        axios({
            method:'get',
            url: '/resendOTP', 
            headers: { 
                phoneNumber: "91" + number,
                login: props.login
            },
        }).then(res=>{  
            setloading(false); 
            setShowOtp(true);
        }).catch(err => { 
            setloading(false);  
            if(err.response) {
                if(err.response.status == 400) {
                    setError(err.response.data.message);
                } 
            } else {
                setError("Something went wrong try again")
            }
        });
    } 
    const handleLogin = (otp) => { 
        if(otp) {
            setloading(true);
            axios({
                method:'get',
                url: '/signinWithPhoneNumber', 
                headers: { 
                    phoneNumber: "91" + number,
                    OTP: otp
                },
            }).then(res=>{   
                showSuccess(true);
                setTimeout(() => {
                    localStorage.setItem("isUserLoggedIn", true);
                    // localStorage.setItem("sessionTimer", Math.round(Date.now() / 1000))
                    // localStorage.setItem("sessionExpire", res.data.accessTokenValidity) 
                    window.location.reload();
                },1000);
            }).catch(err => {
                if(err.response) { 
                    if(err.response.data.errorcode == "005") {
                        showFail(true);
                        setloading(false);  
                    } 
                    if(err.response.data.errorcode == "LG02") {
                        setShowOtp(false);
                        setError("Something went wrong try again") 
                        axios({
                            url: "/signout"
                        }).then(() => { 
                            setloading(false);
                        }).catch(err => { 
                            setloading(false);
                        }) 
                    } 
                } else {
                    setShowOtp(false);
                    setloading(false);  
                    setError("Something went wrong try again")
                }
            });
        }
    }
    return( 
        <> 
            {
                showOtp
                    ?
                <OtpVerify getOtpValue={handleLogin} resendOtp={resendOtp} goBack={() => {
                    setShowOtp(false);
                }}/>
                    :
                <div className="otp-container" style={{flex: 1}}>
                    <h2 style={{fontSize:'20px'}}>Login with Phone Number</h2>
                    <span className="text-muted m-0 py-4">Please enter the registered phone number.</span>
        
                    <div className="form-group form-group-otp">
                        <div className="form-phone">
                            <p>+91</p>
                            <input 
                                type="number" 
                                placeholder="Phone Number" 
                                value={number} 
                                onChange={(e) => setNumber(e.target.value)}
                                className="form-control" 
                            />
                        </div> 
                        {error && <span className="text-error">{error}</span>}
                    </div>
                    <footer className="otp-footer">
                        <p to="/login" className="link hover" onClick={props.signIn}>
                            Go back
                        </p>
                        {loading
                            ?
                        <button className="button-blue d-flex align-items-center">
                            <div className="spinner-border border-white-spin spinner-border-sm mr-2"></div>
                        Loding...</button>
                            :
                        <> 
                        <button className="button-blue" onClick={handleOtp}>Get OTP</button> 
                        </>
                        }
                    </footer>
                </div>  
            } 
        </>
    );
}

export default OtpLogin