import React from 'react'
import './productlist.css';
import EditStockPrice from './editStockPrice/EditStockPrice';
import Product from './Product/Product';
import AppLoader from '../../utils/appLoader/AppLoader';

const ProductList = (props) => {
    
    const [modal,setModal] = React.useState(false); 
    return (
        <div className="container-one"> 
            <div className="product-container">
                <>
                {!props.products 
                        ?
                    <AppLoader />
                        :
                <>
                    {
                        props.products.length > 0 
                            ?
                        <>
                            {props.products.map(product => {
                                return product.sellerVariants.map(variant => {
                                    return (
                                        <Product product={product} variant={variant} setModal={() => setModal(true)}/>
                                    );
                                }); 
                            })}
                        </>
                            :
                        <div class="text-center">No Products Found</div>
                    }
                </>
                }
                    
                </>
                <EditStockPrice open={modal} onClose={() => {
                    setModal(false);
                }}/>
            </div> 
        </div>
    );
}
export default ProductList;