import React from 'react'
import add_photo from '../../../../../assets/icons/add_photo.svg';
import './subvariantimage.css'
import PropModal from '../../../../utils/propmodal/PropModal'
import CropImage from '../../cropImage/CropImage';
import {connect} from 'react-redux'
import {setVariantImage} from '../../../../../containers/productconf/actions';
import AlertDailog from '../../../../utils/alertDailog/AlertDailog';
import { Storage } from 'aws-amplify'; 

const SubVariantImage  = (props) => {
    const [img,setImg] = React.useState([]);
    const [crop,setCrop] = React.useState(false);
    const [cropImg,setCropImg] = React.useState(""); 
    const [alert,showAlert] = React.useState(false);
    const [delIndex,setDelIndex] = React.useState(false);
    const [keys, setKeys] = React.useState([]);

    React.useEffect(() => {
        if(props.state) { 
            props.setVariantImage(img,props.index); 
        }
        if(props.variantImages[props.index]) { 
            setImg([...props.variantImages[props.index]]); 
        } 
    },[props.data,props.state]);

    const setStateImg = (e) => { 
        setCropImg(e.target.files[0]); 
        setCrop(true)
    } 
    const addImg = (url,key) => {
        let images = img;
        let temp = keys;
        temp.push(key);
        images.push(url);
        setImg([...images]);
        setKeys([...temp]);
        setCrop(false);
    }   
    const removeImageS3 = () => {
        showAlert(false);
        let images = img;
        images = images.filter(val => val != img[delIndex]);
        setImg(images);
        const key = keys[delIndex]; 
        Storage.remove(key)
            .then(result => console.log(result))
            .catch(err => console.log(err));
    }   
    return (
        <div className="variant-image-container">
            <h5>{props.name}</h5> 
            <AlertDailog 
                open={alert}
                close={() => showAlert(false)}
                msg={alert}
                onAgree={() => removeImageS3()}
            />
            {crop && 
                <div>
                    <PropModal open={true} onClose={() => {
                        setCropImg(false)
                        setCrop(false)
                    }}>
                        {cropImg && <CropImage src={URL.createObjectURL(cropImg)} image={cropImg} onDone={((url,key) => addImg(url,key)) }/>}
                    </PropModal>
                </div>}
            {img &&  img.map((val,index) => {
                return (
                    <div className="variant-img-content variant-img-content-border"> 
                        <img src={val} className="variant-img-content-img"/>
                        <span
                            onClick={() => { 
                                setDelIndex(index);
                                showAlert("Are you sure? you want to delete this image..")
                            }}
                        >x</span>
                    </div>
                );
            })}
            <div className="variant-img-content">
                <img src={add_photo} alt="Add Photos" className="variant-add-image-icon" />
                <p>Click to Upload</p>
                <input 
                    type="file" 
                    onChange={setStateImg}
                    className="variant-main-multi-img-input"
                />
            </div>
        </div>
    );
}
const mapStateToProps = state => ({
    variantImages: state.productConf.variantImages
});
export default connect(mapStateToProps, {setVariantImage})(SubVariantImage);