import React from 'react';
import {NavLink} from 'react-router-dom';
import './navbar.css';
import logo from '../../assets/img/logo.png';
import dashboard from '../../assets/icons/dashboard.svg';
import store from '../../assets/icons/store.svg';
import notifications from '../../assets/icons/notifications.svg';
import coupons from '../../assets/icons/coupons.svg';
import orders from '../../assets/icons/orders.svg';
import payment from '../../assets/icons/payment.svg';
import reviews from '../../assets/icons/reviews.svg';
import menu from '../../assets/icons/menu.svg';
import Drawer from '@material-ui/core/Drawer';
import {withRouter} from 'react-router-dom';

const excludeNavbarUrls  = new RegExp('/product-configure')

const Navbar = (props) => { 
    const [open, setOpen] = React.useState(false);
    const [nav, setNav] = React.useState(true);
    const toggleDrawer = (open) => (event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
          return;
        }
        setOpen(open);
    }; 
    React.useEffect(() => {
        if(!localStorage.isUserLoggedIn) {
            setNav(false);
        } else {
            if(window.innerWidth > 700 && excludeNavbarUrls.test(props.location.pathname)) {
                setNav(false);
            } else {
                setNav(true);
            }
        }
    });

    const navElements = (
        <div className="side-bar-elements">
            <NavLink exact to="/" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={dashboard} className="side-bar-icon" alt="icon" />
                Dashboard
            </NavLink>
            <NavLink to="/my-store" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={store} className="side-bar-icon" alt="icon" />
                My Store
                </NavLink>
            <NavLink to="/orders" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={orders} className="side-bar-icon" alt="icon" />
                Orders
            </NavLink>
            <NavLink to="/payment" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={payment} className="side-bar-icon" alt="icon" />
                Payment
            </NavLink>
            <NavLink to="/notification" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={notifications} className="side-bar-icon" alt="icon" />
                Notifications
            </NavLink>
            <NavLink to="/tdsrst" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={reviews} className="side-bar-icon" alt="icon" />
                Reviews
            </NavLink>
            <NavLink to="/tssdsdrt" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={coupons} className="side-bar-icon" alt="icon" />
                Coupons
            </NavLink>
            <NavLink to="/my-account" className="side-bar-ele" activeClassName="side-bar-ele-active"> 
                <img src={reviews} className="side-bar-icon" alt="icon" />
                My Account
            </NavLink>
            <NavLink to="/trsast" className="side-bar-ele" activeClassName="side-bar-ele-active">
                <img src={orders} className="side-bar-icon" alt="icon" />
                Support
            </NavLink>
            <div className="side-bar-ele" onClick={props.logout}>
                <img src={orders} className="side-bar-icon" alt="icon" />
                Logout
            </div>
        </div>
    );
    return (
        <div className="navbar-container" style={{display: `${nav ? "flex" : "none"}`}}> 
            <div className="side-bar">
                <nav className="top-bar-left">
                    <div className="mobile-navbar">
                        <React.Fragment>
                            <img src={menu} alt="menu icon" className="menu-icon" onClick={toggleDrawer(true)}/> 
                            <Drawer open={open} onClick={() => setOpen(false)} onClose={toggleDrawer(false)} className="drawer">
                                {navElements}
                            </Drawer>
                        </React.Fragment>
                    </div>
                    <img src={logo} alt="logo" className="nav-left-logo" />
                </nav>
                <div className="desktop-navbar"> 
                    {navElements}
                </div> 
            </div>  
        </div>
    );
}
export default withRouter(Navbar);