import React from 'react'
import './categorylist.css';

import {category} from '../../../data';
import FullScreen from '../../utils/fullscreen/FullScreen';
import Checkbox from '@material-ui/core/Checkbox';
import Paper from '@material-ui/core/Paper';
import { Divider } from '@material-ui/core';
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import AppLoder from '../../utils/appLoader/AppLoader';
 
const CategoryList = (props) => {
    const [modal,setModal] = React.useState(false); 
    const [checked, SetChecked] = React.useState(0);
    const [activeCat, setActiveCat] = React.useState(0); 
    const [category,setCategory] = React.useState(props.category);

    React.useEffect(() => { 
        if(props.catMapping && props.category && props.catMap) {
            let arr = [];
            props.category.forEach(cat => { 
                if(Object.keys(props.catMap).includes(cat.id.toString())) {
                    let subCatMap = props.catMap[cat.id.toString()];
                    let subArr = [];
                    cat.subCategoryList.forEach(subCat => {
                        if(subCatMap.includes(subCat.id)) {
                            subArr.push(subCat);
                        }
                    })
                    arr.push({
                        ...cat,
                        subCategoryList: subArr
                    })
                }
            }); 
            setCategory(arr);
        } else {
            setCategory(props.category);
        }
    },[props.category,props.catMap]);
     
    const setModalClose = () => {
        setModal(false);  
    } 
    return (
        <div className="cat-container">  
            <h1 className="cat-head">Choose Category</h1>
            <div className="cat-cards flex-wrap"> 
                {!category 
                    ?
                    <AppLoder padding={30}/>
                    :
                <>
                    {
                        category.length > 0 
                            ?
                        <>    
                            {category.map((val,index) => { 
                                return (
                                    <div className="cat-card">
                                        <h4>{val.categoryname}</h4>
                                        <p>{val.subCategoryList.map((sub,i) => {
                                            let temp = i != 0 ? "/" : ""
                                            return temp + sub.subCategoryName 
                                        })}</p>
                                        <span
                                            className="hover"
                                            onClick={() => {
                                                setActiveCat(index);
                                                setModal(true); 
                                            }}
                                        >Sell on this category</span>
                                    </div>
                                ); 
                            })}
                        </>
                            :
                        <div>No Categroy Found</div>
                    }
                </>} 
            </div>
            {modal && <FullScreen open={modal} onClose={setModalClose} head="Choose Category">
                <Paper variant="outlined">
                    <div className="modal-content"> 
                        <p className="modal-cat-name">{category[activeCat].categoryname} &nbsp;  </p>
                        <Divider />
                        <div className="modal-col">
                            {category[activeCat].subCategoryList.map((val,index) => {
                                return (
                                    <div className="modal-col-content">
                                        <p>{val.subCategoryName}</p>
                                        <Checkbox
                                            checked={checked == index}
                                            color="primary"
                                            onChange={() => SetChecked(index)}
                                            inputProps={{ 'aria-label': 'primary checkbox' }}
                                        />
                                    </div>
                                );
                            })}
                        </div>
                        <div className="btn-container-center">
                        {category[activeCat].subCategoryList.length > 0
                                ?
                            <button className="btn btn-blue"
                                onClick={() => props.onComplete(category[activeCat].id,category[activeCat].subCategoryList[checked].id)}                                
                            >Continue</button>
                                :
                            <button className="btn btn-blue disabled">No Sub Categories Found</button>
                        }
                        </div>
                    </div>
                </Paper>
            </FullScreen>}
        </div>
    );
}
const mapStateToProps = state => ({
    category: state.appReducer.category,
    catMap: state.appReducer.catMap
});
export default withRouter(connect(mapStateToProps)(CategoryList));