import React from 'react' 
import axios from 'axios' 

const OtpVerify = (props)=> {
    const [otp,otpValue] = React.useState("");
    const [error,setError] = React.useState(false);
    const [loading,setloading] = React.useState(false);
    const [timer,setTimer] = React.useState(false)
    React.useEffect(() => {
        handleCountDown();
    },[]);
    const handleSubmit = () => {
        if(otp.length == 4) {
            setloading(true);
            props.getOtpValue(otp);
        } else {
            setError("Enter valid OTP value")
        }
    } 
    const handleCountDown = (bool = false) => {
        if(bool)
            props.resendOtp();
        let val = 60;
        const term = setInterval(() => {
            val = val - 1;
            setTimer(val);
            if(val == 0) {
                clearInterval(term);
            }
        }, 1000);
    }
    return( 
        <div className="otp-container" style={{flex: 1}}>
            <h2 style={{fontSize:'20px'}}>Enter Otp</h2>
            <span className="text-muted m-0 py-4">Please check your phone and enter 4 digit code that you received.</span>
            
            <div className="form-group">
                <div className="form-group"> 
                    <input 
                        type="number" 
                        placeholder="Enter 4 digit OTP" 
                        value={otp} 
                        onChange = {(e) => otpValue(e.target.value)}
                        className="form-control"
                    />
                </div> 
                {error && <span className="text-error">{error}</span>}
            </div>
            <footer className="otp-footer">
                <div className="d-flex flex-row flex-wrap a-i-c">
                    <p className="font-13" onClick={props.signInLink}>
                        {timer == 0 ? <p className="link hover" onClick={() => handleCountDown(true)}>Resend OTP | </p> : `Resend OTP in ${timer}` }
                    </p>
                    {props.goBack && <p className="link hover m-0" style={{display: "block"}} onClick={props.goBack}>&nbsp;Go Back</p>}
                </div>
                {loading
                    ?
                <button className="button-blue d-flex align-items-center">
                    <div className="spinner-border border-white-spin spinner-border-sm mr-2"></div>
                Loding...</button>
                    :
                <button className="button-blue" onClick={handleSubmit}>Submit</button>
                }
            </footer>
        </div>  
    )
}

export default OtpVerify