import React from 'react'
import './index.css';
import Radio from '@material-ui/core/Radio';

const RadioForm = (props) => {
    return (
        <div className="form-group">
            <label htmlFor="input">{props.displayName ? props.displayName : "" }</label>
            <div className="d-flex a-i-c flex-column">
                <div className="form-radio-group">
                    {props.arr.map((val,index) => {
                        return <div className="radio-grp">
                            <Radio
                                checked={props.value==val} 
                                value={props.value}
                                color="primary" 
                            />
                            <label htmlFor="">{val}</label>
                        </div>
                    })}
                </div>
                {props.error && <span className="text-error">{props.error}</span>}
            </div>
            {props.removeIcon && <span className="remove-form-icon" onClick={props.onRemove}>x</span>}
        </div>
    );
}
export default RadioForm;